import React from "react";
import './FaceRecognation.css';

const FaceRecognation = ({image,box}) => {
    return (
        <div className="center ma">
            <div className="absolute mt2">
                <img id="inputimage" src={image} width="500px" height="auto" alt="pic"></img>
                <div className="box" style={{top : box.topRow, bottom : box.bottomRow, right : box.rightCol, left : box.LeftCol}}></div>
            </div>
        </div>
    );
}


export default FaceRecognation;