import React from "react";
import './ImageLinkForm.css';


const ImageLinkForm = ({inputchange, onclick}) => {
    return(
        <div>
            <p>{"this magic brain will detect faces in your pics! give it a try"}</p>
            <div className="center form pa4 br3 shadow-5">
                <input onChange={inputchange} className="fa4 pa2 w-70 center" type={"text"} placeholder="Enter a Url"></input>
                <button onClick={onclick} className="w-30 grow f4 link ph3 pv2 dib white bg-light-purple">Detect</button>
            </div>
        </div>
    )
}


export default ImageLinkForm;