import React from "react";
import {useState} from "react";
import Navigation from './components/Navigation/Navigation.js';
import Logo from './components/Logo/Logo.js';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import Rank from './components/Rank/Rank.js';
import FaceRecognation from './components/FaceRecognation/FaceRecognation.js';
import Signin from './components/Signin/Signin.js';
import Register from './components/Register/Register.js';
import './App.css';
import Particles from "react-tsparticles";

const App = () => {
  
  const particlesInit = (main) => {
    console.log(main);
  };

  const particlesLoaded = (container) => {
    console.log(container);
  };

  const [input, setInput] = useState('');
  const [image, setImage] = useState('');
  const [box, setBox] = useState({});
  const [route,setRoute] = useState('home');
  const [isSignedIn,setIsSignedIn] = useState(true);
  const [user,setUser] = useState({
    id : "",
    userName : "",
    email : "",
    entries : 0,
    date : new Date()
  })

  const raw = JSON.stringify({
    "user_app_id": {
      "user_id": "wllgy9nm3tc3",
      "app_id": "17058aa0823c4eda8e1b4c11bc6717bb"
    },
    "inputs": [
      {
        "data": {
          "image": {
            "url": input
          }
        }
      }
    ]
  });
  
  const requestOptions = {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Authorization': 'Key beefc9e37eea4d22bcf2809ef476336c'
    },
    body: raw
  };

  const loadUser = (data) => {
    setUser({
      id : data.id,
      userName : data.userName,
      email : data.email,
      entries : data.entries,
      date : new Date()
    })
  }

  const calculateFaceLocation = (data) => {
    const clarifaiFace = data.regions[0].region_info.bounding_box;
    const image = document.getElementById('inputimage');
    const imageWidth = Number(image.width);
    const imageHeight =Number(image.height);
    return {
        LeftCol : clarifaiFace.left_col * imageWidth,
        topRow : clarifaiFace.top_row * imageHeight,
        rightCol : imageWidth - (clarifaiFace.right_col * imageWidth),
        bottomRow : imageHeight - (clarifaiFace.bottom_row * imageHeight)
    }
  }

  const displayFaceBox = (databox) => {
    setBox(databox);
  }

  const inputChange = (event) => {
    let text = event.target.value;
    setInput(text);
  }
  // data ha dar function paiin be onvan argument be display face box dade mishe
  const onClick = () => {
    setImage(input);
    fetch("https://api.clarifai.com/v2/models/face-detection/versions/45fb9a671625463fa646c3523a3087d5/outputs", requestOptions)
      .then(response => response.text())
      .then(result => {
        if (result) {
          fetch('http://localhost:3002/image' , {
            method : "put",
            headers : {'Content-type' : 'application/json'},
            body : JSON.stringify({
                id : user.id
            })
          }).then(response => response.json())
          .then(count => Object.assign(user, {entries : count}));
        }
        displayFaceBox(calculateFaceLocation(JSON.parse(result, null, 2).outputs[0].data))
      })
      .catch(error => console.log('error', error));
  }

  const routeChange = (route) => {
    if (route === "Signout") {
      setIsSignedIn(false);
    }
    else if (route === "home") {
      setIsSignedIn(true);
    }
    setRoute(route);
  }

  return (
  <div className="App">
    <Particles className="particle"
      id="tsparticles"
      init={particlesInit}
      loaded={particlesLoaded}
      options={{
        background: {
          color: {
            value: "",
          },
        },
        fpsLimit: 120,
        interactivity: {
          events: {
            onClick: {
              enable: true,
              mode: "push",
            },
            onHover: {
              enable: true,
              mode: "repulse",
            },
            resize: true,
          },
          modes: {
            bubble: {
              distance: 400,
              duration: 2,
              opacity: 0.8,
              size: 40,
            },
            push: {
              quantity: 4,
            },
            repulse: {
              distance: 200,
              duration: 0.4,
            },
          },
        },
        particles: {
          color: {
            value: "#ffffff",
          },
          links: {
            color: "#ffffff",
            distance: 150,
            enable: true,
            opacity: 0.5,
            width: 1,
          },
          collisions: {
            enable: true,
          },
          move: {
            direction: "none",
            enable: true,
            outMode: "bounce",
            random: false,
            speed: 6,
            straight: false,
          },
          number: {
            density: {
              enable: true,
              area: 800,
            },
            value: 80,
          },
          opacity: {
            value: 0.5,
          },
          shape: {
            type: "circle",
          },
          size: {
            random: true,
            value: 5,
          },
        },
        detectRetina: true,
      }}
    />
      <Navigation routeChange = {routeChange} isSignedIn = {isSignedIn}/>
    {route === "home" ?
      <div>
        <Logo/>
        <Rank name = {user.userName} entries = {user.entries}/>
        <ImageLinkForm inputchange = {inputChange} onclick = {onClick} input = {input}/>
        <FaceRecognation image = {image} box = {box}/>
      </div>
      : route === "Signin" ?  <Signin routeChange = {routeChange} loadUser = {loadUser}/> 
      : <Register routeChange = {routeChange} loadUser = {loadUser}/> 
    }
  </div>
  );
}

export default App;
