import React from "react";


const Navigation = ({routeChange,isSignedIn}) => {
    if (isSignedIn) {
        return (
            <nav style={{display : "flex",justifyContent : "flex-end"}}>
                <p onClick={ () => routeChange("Signout")} className="fe dim black underline pa3 pointer">sign out</p>
            </nav>
        )
    } else {
        return (
            <nav style={{display : "flex",justifyContent : "flex-end"}}>
                <p onClick={ () => routeChange("Signin")} className="fe dim black underline pa3 pointer">sign in</p>
                <p onClick={ () => routeChange("Register")} className="fe dim black underline pa3 pointer">Register</p>
            </nav>
        )
    }
}

export default Navigation;