import React from "react";
import {useState} from "react";


const Signin = ({routeChange, loadUser}) => {
    const [email,setEmail] = useState('');
    const [password,setPassword] = useState('');

    const onEmailChange = (event) => {
        setEmail(event.target.value);
    }

    const onPasswordChange = (event) => {
        setPassword(event.target.value);
    }

    const onSigninClick = () => {
        fetch('http://localhost:3002/signin' , {
            method : "post",
            headers : {'Content-type' : 'application/json'},
            body : JSON.stringify({
                email : email,
                password : password
            })
        })
        .then(response => response.json())
        .then(user => {
            if(user) {
                loadUser(user);
                routeChange('home');
            }
        })
    }

    return (
        <article className="br3 ba b--black-10 mv5 w-100 w-50-m w-25-l mw8 shadow-5 center">
            <main className="pa4 black-80">
                <div className="measure">
                    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                    <legend className="f1 fw6 ph0 mh0">Sign In</legend>
                    <div className="mt3">
                        <label className="db fw6 lh-copy f6" htmlFor ="email-address">Email</label>
                        <input onChange={onEmailChange} className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address"/>
                    </div>
                    <div className="mv3">
                        <label className="db fw6 lh-copy f6" htmlFor ="password">Password</label>
                        <input onChange={onPasswordChange} className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password"/>
                    </div>
                    <label className="pa0 ma0 lh-copy f6 pointer"><input type="checkbox"/> Remember me</label>
                    </fieldset>
                    <div className="">
                    <input onClick={onSigninClick} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Sign in"/>
                    </div>
                    <div className="lh-copy mt3">
                    <p onClick={ () => routeChange("Signup")} className="f6 link dim black db pointer">Register</p>
                    <a href="#0" className="f6 link dim black db">Forgot your password?</a>
                    </div>
                </div>
            </main>
        </article>
    );
}


export default Signin;